import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl } from '@angular/forms';

@Component({
  selector: 'app-form-editor2',
  templateUrl: './form-editor2.component.html',
  styleUrls: ['./form-editor2.component.scss']
})
export class FormEditor2Component implements OnInit {

  detailsForms = new FormGroup({
      firstName : new FormControl(),
      lastName :  new FormControl()
  }) 
  constructor() { }

  data : string = "TestData";

  ngOnInit(): void {

    setTimeout(()=>{
      this.detailsForms.patchValue({
        firstName : 'Clyde',
        lastName  : 'Correa'
      })
    },5000)
  }

  onSubmit() {    
    console.warn(this.detailsForms.value);
  }

}
